import React from 'react';
import "./About.scss";
import colosseo from './colosseo.png';
import sun from './sun.png';
import Radium, {StyleRoot} from 'radium';
import { slideInLeft, slideInRight, slideInUp, fadeIn } from 'react-animations';

const styles = {
  slideInLeft: {
    animation: 'x 1s',
    animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
  },
  slideInRight: {
    animation: 'x 3s',
    animationName: Radium.keyframes(slideInRight, 'slideInRight')
  },
  slideInUp: {
    animation: 'x 3s',
    animationName: Radium.keyframes(slideInUp, 'slideInUp')
  },
  fadeIn: {
    animation: 'x 3s',
    animationName: Radium.keyframes(fadeIn, 'fadeIn')
  }
}

export default function About() {
  
  return (
    <>
      
      
      <StyleRoot>
      <div className="rightpart">
        <div className="images"  style={styles.slideInLeft}></div>
        <img src={sun} className="sun" style={styles.slideInUp} />
        <img src={colosseo} className="colosseo" style={styles.slideInRight} />
        
        
      </div>
      
      <div className="leftpart">
        <h3 style={styles.fadeIn} >This website was fully coded by its owner <br/> Using ReactJS mainly.
        All photos displayed on this site were also shot by the owner.<br/>
        You can contact me using any of the means found on the site's footer or by clicking <a href="mailto:jaafaryassine.27@gmail.com">here</a></h3>
      </div>
      </StyleRoot>
    </>
  )
}
